<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <a-alert
                message="功能提示"
                description="显示登录用户发起的表决，拥有编辑、删除操作。已经开始的表决不允许编辑、删除操作。"
                type="info"
                showIcon
        />
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from '../表决列表/components/TopButtons'
    import {getUserVoteList, deleteVote} from '@/api/vote'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            TopButtons, filterComponent, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                VoteId: 0,
                visible: false,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '标题',
                        dataIndex: 'title',
                        key: 'title',
                    },
                    {
                        title: '是否公示',
                        dataIndex: 'is_publicized_format',
                        key: 'is_publicized_format',
                    },
                    {
                        title: '表决开始时间',
                        dataIndex: 'started_at',
                        key: 'started_at',
                    },
                    {
                        title: '表决结束时间',
                        dataIndex: 'ended_at',
                        key: 'ended_at',
                    },
                    {
                        title: '公示时间',
                        dataIndex: 'published_at',
                        key: 'published_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            edit: {visible: true, disable:  function (row) {
                                    let now = moment().valueOf() / 1000;
                                    if (row.started_at > now) {
                                        return false;
                                    }
                                    return true;
                                }},
                            edit2: {
                                visible: true, disable: function (row) {
                                    if (!row.is_publicized) {
                                        return true;
                                    }
                                    if (row.published_at) {
                                        return true;
                                    }
                                    let now = moment().valueOf() / 1000;
                                    if (row.ended_at < now) {
                                        return false;
                                    }
                                    return true;
                                }, title: '编辑结果'
                            },
                            delete: {visible: true, disable:  function (row) {
                                    let now = moment().valueOf() / 1000;
                                    if (row.started_at > now) {
                                        return false;
                                    }
                                    return true;
                                }},
                        }
                    }
                ],
                tableRequest: getUserVoteList,
                filterFields:[
                    {label:'开始时间',field:'start_timestamp'},
                    {label:'结束时间',field:'end_timestamp'},
                    {label:'关键字',field:'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleOperate(type, row) {
                let id = row.id;
                switch (type) {
                    case 'edit':
                        this.$router.push({name: '表决编辑', params: {id: id}})
                        break;
                    case 'edit2':
                        this.$router.push({name: '表决编辑公示结果', params: {id: id}})
                        break;
                    case 'delete':
                        deleteVote(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>
